.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: stretch;
  justify-content: stretch;
}

.App-header {
  background-color: #282c34;
  height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: top;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  background-color: #3a3f4b;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: top;
  justify-content: top;
  color: white;
}

.App-row {
  min-height: 5vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding:8px;
  color: white;
}

.App-row-text {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding:8px;
  font-size: calc(6px + 2vmin);
  color: white;
}

.App-row-input {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding:8px;
  font-size: calc(2vmin);
  color: rgb(0, 0, 0);
}

.App-button-row {
  min-height: 64px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 4px;
  padding: 4px;
  color: white;
}

.App-picker-button {
  width: 32px;
  height: 32px;
  padding: 2px;
  margin: 2px;
  text-align: center;
  font-size: 18px;
  color: rgb(0, 0, 0);
}

.App-download-button {
  width: 64px;
  height: 32px;
  padding: 2px;
  margin: 2px;
  text-align: center;
  font-size: 18px;
  color: rgb(0, 0, 0);
}

.App-picker {
  background-color: #ffffff00;
  min-height: 192px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin:32px;
  color: white;
}

.App-picker-l {
  background-color: #ffffff00;
  min-height: 192px;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-picker-l-row {
  background-color: #ffffff00;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content:center;
  color: white;
}

.App-picker-l-row-l {
  background-color: #e6151500;
  width: 128px;;
  text-align: right;
  margin-top:8px;
  margin-bottom:10px;
  color: white;
}

.App-picker-l-row-r {
  background-color: #ffffff00;
  width: 192px;
  display: flex;
  align-items: stretch;
  justify-content: stretch;
  margin:8px;
  color: rgb(0, 0, 0);
}

.App-picker-l-row-b {
  width: 24px;
  height: 24px;
  color: rgb(0, 0, 0);
}

.App-picker-r {
  background-color: #ffffff00;
  height: 192px;
  width: 256px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-picker-r-u {
  background-color: #00f7ff00;
  height: 160px;
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-picker-r-d {
  background-color: #ec1eff00;
  height: 32px;
  width: 80%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  color: white;
}

.App-picker-r-d-l {
  background-color: #ffffff00;
  margin:8px;
  width:128px;
  text-align: right;
  color: white;
}

.App-picker-r-d-r {
  margin:8px;
  width:80px;
}

